.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.main {
  margin: 0 auto;
  margin-top: 100px;
  width: 70vw;
  border-radius: 20px;
}

.main-inner {
  overflow: hidden;
  border-radius: 20px;
  background: #fff;
  min-height: 600px;
  display: flex;
  box-shadow: 0px 0px 60px 14px rgba(0, 2, 4, 0.2);
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}